@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@200;300;400;500;600;700&display=swap);
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
:root {
    --bgGrey: #343333;
    --darkGrey: #4D4D4D;
    --offWhite: #E7E7E7;
    --lightGreen: #8CD197;
    --midGreen: #78997C;
}
h1 {
	font-family: "Oswald", sans-serif;
	text-transform: uppercase;
	font-weight: 500;
	font-size: 4rem;
	color: var(--offWhite);
	margin-top: 3rem;
	margin-bottom: 3rem;
    text-align: center;
}

@media screen and (max-width: 280px) {
    h1 {
        font-size: 3.5rem;
    }
}

h2 {
    font-family: "Oswald", sans-serif;
	text-transform: uppercase;
	font-weight: 500;
	font-size: calc(30px + 1.5vw);
    color: var(--offWhite);
    text-align: center;
    padding-top: 1rem;
    padding-bottom: 1rem;
}

h3 {
    font-family: "Oswald", sans-serif;
	text-transform: uppercase;
	font-weight: 500;
	font-size: calc(16px + 1.5vw);
    color: var(--darkGrey);
    text-align: center;
}

.h3alternate {
    font-size: calc(16px + 1.5vw);
    color: var(--offWhite);
    padding-top: 1rem;
    padding-bottom: 1rem;
}

.kitColourSpacer {
    margin-top: 2rem;
}

label {
    font-family: "Oswald", sans-serif;
	text-transform: uppercase;
	font-weight: 500;
	font-size: calc(16px + 1.5vw);
    color: var(--darkGrey);
    text-align: center;
    margin-right: 1rem;
}

label.headerLabel {
    display: block;
    margin-top: 1rem;
}

input {
    font-family: "Oswald", sans-serif;
	font-weight: 400;
	font-size: calc(12px + 1.5vw);
	color: var(--darkGrey);
}

button {
    font-family: "Oswald", sans-serif;
    text-transform: uppercase;
	font-weight: 500;
	font-size: calc(12px + 1.5vw);
	color: var(--darkGrey);
}

p {
    font-family: "Oswald", sans-serif;
	font-weight: 400;
	font-size: calc(12px + 1.5vw);
    color: var(--darkGrey);
    padding-top: 0.4rem;
    padding-bottom: 0.4rem;
}

.errorMessage {
    font-family: "Oswald", sans-serif;
	font-weight: 400;
	font-size: calc(12px + 1.5vw);
    color: rgb(216, 27, 27);
    padding-top: 0.4rem;
    padding-bottom: 0.4rem;
}

.errorMessageHidden {
    display: none;
}

a {
    font-family: "Oswald", sans-serif;
	text-transform: uppercase;
	font-weight: 500;
	font-size: calc(16px + 1.5vw);
    color: var(--offWhite);
    text-align: center;
    padding-top: 1rem;
    padding-bottom: 1rem;
    text-decoration: none;
}

a:hover {
    text-decoration: underline;
}
.kitButton {
    border: none;
    border-radius: 100%;
    height: 1.5rem;
    width: 1.5rem;
    margin-left: 0.5rem;
    margin-right: 0.5rem;
    padding: 0;
    box-sizing: border-box;
}

.kitButton:hover {
    box-shadow: 0 0 0 4px var(--bgGrey);
}

.kitButtonHolder {
    border-color: var(--midGreen); 
    border-style: solid;
    border-width: 1px;
    border-radius: 40px;
    background-color: var(--midGreen);
    padding: 0.5rem 0rem 0.5rem 0rem;
    width: 50%;
    margin: auto;
    margin-top: 1rem;
    margin-bottom: 3rem;
}

@media screen and (min-width: 769px) {
    .kitButton{
        height: 2rem;
        width: 2rem;
    }
    .kitButtonHolder {
        width: 40%;
    }
}

/* For smaller mobile sizes */
@media screen and (max-width: 414px) {
    .kitButtonHolder {
        width: 90%;
    }
}

/* For Galaxy Fold screen size */
@media screen and (max-width: 280px) {
    .kitButton{
        height: 1.4rem;
        width: 1.4rem;
        
    }
}

.kit1 {
    background-color: rgb(87, 224, 255);
}

.kit2 {
    background-color: rgb(24, 255, 70);
}

.kit3 {
    background-color: rgb(255, 229, 29);
}

.kit4 {
    background-color: rgb(216, 27, 27);
}

.kit5 {
    background-color: hotpink;
}

.kitSelected {
    box-shadow: 0 0 0 4px var(--bgGrey);
    color: var(--bgGrey)
}

.chosenKitColour {
    border: none;
    border-radius: 50%;
    height: 2rem;
    width: 2rem;
    margin-left: 2rem;
    margin-right: 0.5rem;
}

.pillButton {
    border: 1px solid var(--lightGreen);
    background-color: var(--lightGreen);
    padding: 0.5rem 2rem;
    border-radius: 50px;
    text-align: center;
    vertical-align: middle;
    margin: 1rem;
}

.pillButton:hover {
    background-color: var(--midGreen);
    border: 1px solid var(--midGreen);
    cursor: pointer;
}

.listButtons {
    display: flex;
    align-items: center;
    justify-content: center;
    padding-bottom: 2rem;
}

.playersButtons {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    padding-top: 1rem;
    padding-bottom: 1rem;
}

/* For Galaxy Fold screen size */
@media screen and (max-width: 280px) {
    .listButtons {
        display: grid;
    }
}

/* Removed default outline focus on kit buttons, but have replaced with box shadow outline so it's still clearly visible when selected. Checked it would pass contrast ratio check here: https://webaim.org/resources/contrastchecker/ */

button:focus {
    outline:0;
} 

footer {
    background-color: var(--darkGrey);
    width: 100%;
    height: 7rem;
    color: var(--offWhite);
    display: flex;
    align-items: center;
    justify-content: center;
}
input {
    border: none;
    background: var(--offWhite);
    padding: 0.5rem 2rem;
    text-align: center;
    margin-top: 1rem;
    margin-bottom: 1rem;
}
form {
    margin: 1.5rem;
}

.cardHolder {
    display: grid;
    border-radius: 10px; 
    background-color: var(--offWhite);
    margin: auto;
    margin-bottom: 2rem;
    text-align: center;
    width: 90%;
    box-shadow: 2px 7px 19px 0px rgba(0,0,0,0.51); 
    -webkit-box-shadow: 2px 7px 19px 0px rgba(0,0,0,0.51);
    -moz-box-shadow: 2px 7px 19px 0px rgba(0,0,0,0.51);
}

/* For smallest screen sizes */
@media screen and (max-width: 360px) {
    .cardHolder {
        margin: auto;
        margin-bottom: 2rem;
    }
}

@media screen and (min-width: 769px) {
    .cardHolder {
        margin: auto;
        margin-top: 2rem;
        margin-bottom: 2rem;
    }
}

.cardGreen {
    border-radius: 0; 
    background-color: var(--lightGreen);
    text-align: center;
    padding-top: 1rem;
    padding-bottom: 1rem;
}

.cardWhite {    
    border-radius: 0; 
    background-color: var(--offWhite);
    text-align: center;
    padding-top: 1rem;
    padding-bottom: 1rem;
}

.cardTop {
    background-color: var(--darkGrey);
    border-radius: 10px 10px 0 0; 
}

.cardBody{
    border-radius: 0; 
}

.cardBottom {
    background-color: var(--darkGrey);
    border-radius: 0 0 10px 10px ; 
    display: inline-block;
    vertical-align: middle;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    min-height: 4rem;
}

@media screen and (max-width: 768px) {
    .cardBottom {
        display: grid;
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
        min-height: 3.5rem;
    }
}

.kitAndName {
    display: flex;
    justify-content: center;
    align-items: center;
}

.teamsDivider {
    width: 100%;
    background: linear-gradient(90deg, var(--lightGreen), var(--midGreen) 20% 80%, var(--lightGreen));
    height: 2px;
    margin: auto;
    margin-bottom: 3rem;
}


.twoLists {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 1fr;    
}

@media screen and (max-width: 768px) {
    .twoLists {
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        grid-template-rows: 1fr;    
    }
}
@media screen and (min-width: 769px) {
    .playerColumns {
        -webkit-columns: 2;
                columns: 2;
    }
}

.playerRows {
    display: inline-block;
    grid-template-rows: repeat(2);
    margin: 1rem; 
    margin-bottom: 0;
}

.playerLabel {
    text-align: left;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background-color: var(--bgGrey);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


